import React from 'react';
import PropTypes from 'prop-types';
import ScoreBoard from './ScoreBoard.jsx';
import '../../../stylesheets/PlayerList.scss';

const ScoreBoardList = ({
  playernames = [], onFetchAlmonds = f => f,
}) =>
  (<div className="player-list">
    {(playernames.length === 0) ?
      <p>...loading players</p> :
      playernames.map(player =>
        (<ScoreBoard
          {...player}
          key={player.user_id}
          onFetchAlmonds={() => onFetchAlmonds()}
        />))
    }
  </div>);

ScoreBoardList.propTypes = {
  playernames: PropTypes.array,
  onFetchAlmonds: PropTypes.func.isRequired,
};

ScoreBoardList.defaultProps = {
};

export default ScoreBoardList;
