import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../../../stylesheets/OwnerReport.scss';
import { MessageBoardId, Invites, Orders } from '../containers';

class OwnerReport extends Component {
  constructor(props) {
    super(props);
    this.state = {
      scores: this.props.scores,
      participation: this.props.participation,
    };
  }
  render() {
    const {
      id, expires, orders,
    } = this.props;
    return (
      <div id="owner-report">
        <MessageBoardId />
        {
(this.props.info !== 'Admin') ?
  <p><strong>{this.props.info}</strong></p> :
  <table>
    <tr>
      <th>Square</th>
      <th>Total</th>
      <th>High Pick</th>
    </tr>
    {
  this.state.scores.map((scoredata, i) =>
  (i % 2 === 0 ? (
    <tr className="even">
      <td className="lineitem">{scoredata.name} </td>
      <td className="lineitem">{scoredata.total}</td>
      <td className="lineitem">{scoredata.highPick}</td>
    </tr>
 ) : (
   <tr className="odd">
     <td className="lineitem">{scoredata.name} </td>
     <td className="lineitem">{scoredata.total}</td>
     <td className="lineitem">{scoredata.highPick}</td>
   </tr>
  )
  ))
  }
    <br />
    <tr>
      <th>Email</th>
      <th>Requested</th>
      <th>Accepted</th>
      <th>Locked</th>
    </tr>
    {
    this.state.participation.map((participatedata, i) =>
    (i % 2 === 0 ? (
      <tr className="even">
        <td>{participatedata.email}</td>
        <td>{participatedata.requested}</td>
        <td>{participatedata.accepted}</td>
        <td>{participatedata.locked}</td>
      </tr>
  ) : (
    <tr className="odd">
      <td>{participatedata.email}</td>
      <td>{participatedata.requested}</td>
      <td>{participatedata.accepted}</td>
      <td>{participatedata.locked}</td>
    </tr>
  )
  ))
  }
  </table>
        }
        <Invites game_id={id} expired={expires} orders={orders} />
      </div>
    );
  }
}

OwnerReport.propTypes = {
  id: PropTypes.string.isRequired,
  scores: PropTypes.array,
  participation: PropTypes.array,
  orders: PropTypes.array,
  expires: PropTypes.string.isRequired,
};

OwnerReport.defaultProps = {
};

export default OwnerReport;

