import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../../../stylesheets/SendInvitePage.scss';
import { Reports, GameReport } from '../containers';

class SendInvitePage extends Component {
  state = {
    gameId: this.props.match.params.gameId,
    gameName: this.props.gameName,
    onFetchAvailableInvites: this.props.onFetchAvailableInvites,
  };

  componentDidMount() {
    this._asyncRequest = this.props.onFetchAvailableInvites(this.state.gameId);
    setTimeout(() => { // Brute force fix if user hits refresh
      this.setState({
        gameName: this.state.gameName,
      });
    }, 250);
  }

  componentWillUnmount() {
    if (this._asyncRequest) {
      this._asyncRequest.cancel();
      this.setState({
        gameName: null,
      });
    }
  }

  render() {
//    const {
//      location,
//    } = this.props;
    return (
      (
          <div id="send-invite">
            <br />
            <br />
            <br />
            <h1>Survey Dashboard</h1>
            <br />
            <br />
            <GameReport />
            <br />
            <br />
            <Reports />
          </div>
      )
    );
  }
}

SendInvitePage.propTypes = {
  onFetchAvailableInvites: PropTypes.func,
};

SendInvitePage.defaultProps = {
  onFetchAvailableInvites: f => f,
};

export default SendInvitePage;
