export const Company = () =>
  (<section className="company">
    <br />
    <br />
    <br />
    <br />
    <h2>WildAlmonds</h2>
      WildAlmonds is a platform built around an exceptionally easy to use Drag and Drop functionality. Our initial
      version is centered around a stacked rank voting system. With your help and participation, there is much more to
      come based on this philosophy and technical solution.
    <h3> Why start with a stack ranked voting system?</h3>
      Companies should be fun, flexible, and sustainable. Often, teams are faced with multiple options rather
      than a single choice. Using the wisdom of the crowd philosophy (what would please the most individuals or what
      most believe to be the most likely) greatly improves the final outcome. Basically, we all need to have access to
      an easy experience that gets us to a what matters decision quickly.
    <strong>To that end!</strong>
      We believe in WildALmonds so much, we use this solution to build the site. By asking our user base (You!) what is
      most important, we can gauge where our efforts will please the most of our customers. This will allow us to grow
      partnerships and business creating a true win-win. If WildAlmonds works for us, it will work for you. There are
      several use cases where the core concepts outlined here fit exceptionally well and we are junkies for those big
      ideas. We do however strongly believe that the core of this survey option will drive WildAlmonds strategically
      to those new markets and opportunities. For these reasons, the survey portion of the site is not going anywhere
      and must continue to grow in order to sharpen our focus.
    <h3> So how do I use it?</h3>
      The solution works on 4 simple core concepts:
    <h4>Creation:</h4>
      Create an account at <a href="https://wildalmonds.com/signup">Signup</a>
      Once you have an account, the Create Tournament option is available from the Owner Page. Currently, the Create
      page allows you a 4x4 Survey: 4 Almonds and 4 Squares. We can easily create you a custom Survey outside the
      Demo restrictions. Your free 4x4 Survey allows for 3 free invites. This can also be extended outside the DEMO
      restrictions.
    <h4>Invitation:</h4>
      Now that you have completed the Creation phase, you are ready to send your invitations to your participants.
      In the Owner Dashboard, you will your Tournament as a Box. Within that box is the Event box. Click or touch the
      Event Box. This displays your voting results as well as your invite cards. Our initial version sends invites via
      Email from the wildalmondz@gmail.com account. Please have your invitees search for a message from this sender.
      Fill that out for a new user and hit the <strong>Send It!</strong> button.
    <h4>Participation:</h4>
      Now we are getting to the cool stuff! Your participants receive within the email their own unique link to your
      WildAlmonds Event. Clicking the link sends them to our site where they are greeted with your survey owner and
      the description summary of what the survey is about. There, they choose the Accept button to link to the Event
      page. This page allows your participant to rank their choices via Almonds for your Squares. This is done via
      touchscreen or mouse by dragging the Almonds above the Square and releasing it. Once all picks are completed,
      the participant receives the Lock Picks button. More detail are available here at
    <a href="https://wildalmonds.com/about/help">How to Play</a>
    <h4>Aggregation:</h4>
      Each Survey request sent appears in your Event report. We do not show which picks your users individually
      selected, we total those up. You will see when you sent the request, when the user last Accepted, and when the
      picks where Locked. For best results, please strongly encourage your participants to Lock their picks. As they
      change those picks, your results will change in your summarized totals.
    <h4>That is it!</h4>
      We have just walked through the solution end-to-end. It is simply a matter of inviting more
      participants to the Event via the Owner Panel. As results come in, you may see that you have a passionate base
      that supports a single option, but the Total for another is highest. This is your wisdom of the crowd
      selection and it deserves a consideration. The more invites you send, the better this reporting will be.
    <h4>Reminder</h4>
      We can easily customize the number of Invitations, Almonds and Squares for you. We need your help to grow the
      site. Please do give us your feedback, invite us to your Events, and let us earn your trust and partnership.
      Contact us at wildalmondz@gmail.com. We would love to hear from you. Or, setup a tournament with your own
      account and ask us through the application.
  </section>);

export const Help = () =>
  (<section className="help">
    <br />
    <br />
    <br />
    <br />
    <h1>How to play</h1>
    <p>
      WildAlmonds is a vote by ranking system built around drag and drop functionality. It is an invite system,
      meaning that market barriers like external account creation is removed. All the participant needs is to follow
      the provide link to our webpage to participate. They get a unique url just for them and your event!

    </p>
    <h3>Play by Invite</h3>
    <p>
      The Admin of the Event sends you a message including your very unique link. Your link is not secure so do not
      share it with others. Once you follow the link, click the image for the event.
      To secure your participation and see all the events you have participated in, create an account and join
      WildAlmonds by using Signup. With or without an account, our site allows you to
      return to the page later to view your picks.
    </p>
    <h3>Play securely</h3>
    <p>
      Future functionality will allow participation from within the app if you have created a secure account.
      Currently participation only requires a link and as long as that is not shared, your picks are secure.
      As of 02/19/2020 your picks individual selections cannot be viewed by the admin but are visible in the
      overall totals for the event.
    </p>

    <h3>Making a selection</h3>
    <p>
      Use your mouse or touch and drag an almond to a square. Note that the Almond is a strike out and the number
      of the Almond is attached to the square. Your picks are written to the database as they are made. You will be
      able to edit your picks until either the event expires or you choose to Lock Picks.
    </p>
    <h3>Deleting a selection</h3>
    <p>
      Click an Almond that has been selected. Select OK when prompted to remove the pick.
    </p>
    <h3>Locking the picks</h3>
    <p>
      Once all picks have been made <strong>Picks Remain: 0</strong> the Lock Picks button will appear. Click the
      Lock Picks button and either confirm that you are committed to your selections but choosing OK or return for
      futher picks. The admin of the tournament can see when you have accepted and when you lock your picks. They
      currently cannot see what you have chosen.
    </p>
   </section>);
