import React from 'react';
import PropTypes from 'prop-types';
import Almond from './Almond.jsx';
import ItemTypes from './ItemTypes';
import '../../../stylesheets/AlmondList.scss';

// https://medium.com/@rajaraodv/a-guide-for-building-a-react-redux-crud-app-7fe0b8943d0f
// need to import the function to check if the Almond can be drug

const AlmondList = ({
  almonds = [], game_id, userId, isDropped, locked, onDragAlmond = f => f, onFetchInvitedAlmonds = f => f, onFetchInvitedSquares = f => f, onDeletePick = f => f,
}) =>
  (<section id="almond-list">
    {(almonds.length === 0) ?
      <p>...loading almonds</p> :
      almonds.map(almond =>
        (<Almond
          isDropped={isDropped}
          game_id={game_id}
          userId={userId}
          locked={locked}
          {...almond}
          type={ItemTypes.ALMOND}
          key={almond.almond_id}
          onDragAlmond={() => onDragAlmond(almond.isDropped)}
          onFetchInvitedAlmonds={() => onFetchInvitedAlmonds(game_id, userId)}
          onFetchInvitedSquares={() => onFetchInvitedSquares(game_id, userId)}
          onDeletePick={() => onDeletePick(game_id, userId, almond.almond_id)}
        />))
    }
   </section>);

AlmondList.propTypes = {
  game_id: PropTypes.string.isRequired,
  userId: PropTypes.string.isRequired,
  almonds: PropTypes.array,
  isDropped: PropTypes.bool,
  onDragAlmond: PropTypes.func.isRequired,
  onFetchInvitedAlmonds: PropTypes.func.isRequired,
  onFetchInvitedSquares: PropTypes.func.isRequired,
  onDeletePick: PropTypes.func.isRequired,
};

AlmondList.defaultProps = {
  isDropped: false,
};

export default AlmondList;
