import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import '../../../stylesheets/InviteCard.scss';


function findInvitedUserId(gameId, inviteId, email, onFetchUserId) {
  onFetchUserId(gameId, inviteId, email);
}

class InviteCard extends Component {
  state = {
    gameId: this.props.match.params.gameId,
    inviteId: this.props.match.params.emailId,
    email: this.props.match.params.email,
    gameName: this.props.game_name,
    fromAddress: this.props.email.email,
    onFetchUserId: this.props.onFetchUserId,
  };

  componentDidMount() {
    this._asyncRequest = findInvitedUserId(
      this.state.gameId,
      this.state.inviteId,
      this.state.email,
      this.state.onFetchUserId
    );
  }

  componentWillUnmount() {
    if (this._asyncRequest) {
      this._asyncRequest.cancel();
    }
  }

  render() {
    const {
      game_name, game_description, history,
    } = this.props;
    return (
      <section
        className="invite-card"
        style={this.style}
      >
        <div>
          <ul>
            <li>
              <h4>Invitation Card</h4>
            </li>
            <li>
              <strong>From:</strong> {this.props.email.email}
            </li>
            <li>
              <strong>Event:</strong> {game_name}
            </li>
            <li>
              <strong>Description:</strong> {game_description}
            </li>
          </ul>
        </div>
        <div id="accept">
          <button
            onClick={() => {
              history.push(`/events/invitedgame/${this.state.gameId}/${this.props.userId.id}`);
            }}
          >Accept
          </button>
        </div>
      </section>
    );
  }
}

InviteCard.propTypes = {
  game_id: PropTypes.string.isRequired,
  game_name: PropTypes.string.isRequired,
  game_description: PropTypes.string.isRequired,
  userId: PropTypes.string,
  onFetchUserId: PropTypes.func,
};

InviteCard.defaultProps = {
  onFetchUserId: f => f,
};

export default withRouter(InviteCard);
