class Container extends Component {
  render() {
    const {
      game_id, userId, picks, expiretime, locked, history,
    } = this.props;
    return (
      <section className="container" style={this.style}>
        <section className="container-almonds">
          <strong>Drag and drop to a square below</strong>
          <Almonds game_id={game_id} userId={userId} locked={locked} />
        </section>
        <section className="container-squares">
          <Squares />
        </section>
        <section className="container-supporting">
          <div id="expire-time">
            <CountdownId expires={expiretime} />
          </div>
          <div id="lock-picks">
            <div>{((picks === 0) && (locked === null)) ?
              <button
                type="button"
                onClick={() => {
                  lockPicks(game_id, userId, picks, locked, history);
                }}
              >Lock picks
              </button>
            : `Picks Remain ${picks}`}
            </div>
          </div>
        </section>
      </section>
    );
  }
}

Container.propTypes = {
  game_id: PropTypes.string.isRequired,
  userId: PropTypes.string.isRequired,
  picks: PropTypes.number,
  expiretime: PropTypes.string,
  locked: PropTypes.string,
};

module.exports = withRouter(DragDropContext(MultiBackend(HTML5toTouch))(Container));
