import React from 'react';
import { withRouter } from 'react-router-dom';
import storeFactory from '../../store/index';
import PropTypes from "prop-types";

function writeTournament(eventname, description, square1, square2, square3, square4, onWriteName) {
  // Below line works adding state to the client so user_id can be found
  onWriteName(eventname, description, square1, square2, square3, square4,);
}

class ConfirmNewTournament extends React.Component {
  state = {
    eventname: this.props.match.params.eventname,
    description: this.props.match.params.description,
    square1: this.props.match.params.square1,
    square2: this.props.match.params.square2,
    square3: this.props.match.params.square3,
    square4: this.props.match.params.square4,
    onWriteName: this.props.onWriteName,
  };

  componentDidMount() {
    this._asyncRequest = writeTournament(
      this.state.eventname,
      this.state.description,
      this.state.square1,
      this.state.square2,
      this.state.square3,
      this.state.square4,
      this.state.onWriteName
    );
    this.props.history.push(`/owner/dashboard`);
  }

  componentWillUnmount() {
    if (this._asyncRequest) {
      this._asyncRequest.cancel();
    }
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }

  render() {
    const {
      onWriteName
    } = this.props;

    return (
      <form >
        <div id="name-details">
          <h2>Add Squares</h2>
          <label>
            Square 1
            <input
              name="square1"
              type="text"
              className="form-control"
              placeholder="Name"
              maxLength="20"
              value={this.state.square1}
              onChange={this.handleInputChange}
              required
            />
          </label>
          <label>
            Square 2
            <input
              name="square2"
              type="text"
              className="form-control"
              placeholder="Name"
              maxLength="20"
              value={this.state.square2}
              onChange={this.handleInputChange}
              required
            />
          </label>
          <label>
            Square 3
            <input
              name="square3"
              type="text"
              className="form-control"
              placeholder="Name"
              maxLength="20"
              value={this.state.square3}
              onChange={this.handleInputChange}
              required
            />
          </label>
          <label>
            Square 4
            <input
              name="square4"
              type="text"
              className="form-control"
              placeholder="Name"
              maxLength="20"
              value={this.state.square4}
              onChange={this.handleInputChange}
              required
            />
          </label>
        </div>
        <button onClick={() => {onNewSquares(
          this.state.eventname,
          this.state.description,
          this.state.square1,
          this.state.square2,
          this.state.square3,
          this.state.square4,
        )}}>Next</button>
      </form>
    )
  }
}

ConfirmNewTournament.propTypes = {
  onWriteName: PropTypes.func,
};

ConfirmNewTournament.defaultProps = {
  onWriteName: f => f,
};

export default withRouter(ConfirmNewTournament);
