import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
// import Container from './Container';
import { ContainerId } from '../containers';
import Whoops404 from './Whoops404';
import '../../../stylesheets/GameDetails.scss';

// https://stackoverflow.com/questions/40352310/how-do-you-mix-componentdidmount-with-react-redux-connect

class GameDetails extends Component {
  componentDidMount() {
    // alert('*~*~*~*~*~*~*~*~*~*~*~*~componentDidMount for GameDetails *~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~');
  }

  componentWillUnmount() {
    // alert('*~*~*~*~*~*~*~*~*~*~*~*~componentDidUnMount for Game *~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~');
  }

  render() {
    const {
      game_id, game_name, location,
    } = this.props;
    return (
      ((!game_id) ?
        <Whoops404 location={location} /> :
        <section className="game-details" style={this.style}>
          <div className="wrapper">
            <h1>{game_name}</h1>
            <ContainerId game_id={game_id} />
          </div>
        </section>)
    );
  }
}

GameDetails.propTypes = {
  game_id: PropTypes.string.isRequired,
  game_name: PropTypes.string.isRequired,
  onFetch: PropTypes.func,
};

GameDetails.defaultProps = {
  onFetch: f => f,
};

export default GameDetails;
