import React from 'react';
import PropTypes from 'prop-types';
import BoardSquare from './BoardSquare_Container.jsx';
import ItemTypes from './ItemTypes';

import '../../../stylesheets/SquareList.scss';

/*
function handleDrop(squareId, item) {
  const { name } = item;
  alert(`Drop defined?: ${name}`);
}
*/

const SquareList = ({ squares = [], onDrop = f => f }) =>
  (<div className="squares-list">
    {
      squares.map(square =>
        (<BoardSquare
          {...square}
          key={square.square_id}
          // lastDroppedItem={null}
          accepts={ItemTypes.ALMOND}
          onDrop={() => onDrop(item => this.handleDrop(square.square_id, item))}
        />))
    }
  </div>);

SquareList.propTypes = {
  squares: PropTypes.array,
  onFetch: PropTypes.func,
  onDrop: PropTypes.func,
};

export default SquareList;

