export const OwnerDashboardId = connect(
  state =>
    ({
      messagetext: state.message.text,
      time: state.servertime.time,
      expiretime: state.expiretime.expires,
    }),
  dispatch =>
    ({
      onFetchOwnerTournaments() {
        dispatch(checkAuth());
        dispatch(fetchOwnerTournaments());
      },
      onFetchAuthStatus() {
        dispatch(checkAuth());
      },
    }),
)(OwnerDashboard);

