export const InvitePendingId = connect(
  state =>
    ({
      report: state.ownerReportsList.reports,
    }),
  dispatch =>
    ({
      onSendInvite(userId, gameId, gameName, email, emailId, inviteId) {
        dispatch(writeInvitedUser(userId, gameId, gameName, email, emailId, inviteId));
      },
      onFetchAvailableInvites(gameId) {
        dispatch(fetchAvailableInvites(gameId));
      },
      onFetchOwnerReport(gameId) {
        dispatch(fetchOwnerReport(gameId));
      },
    }),
)(InvitePending);

