export const Almonds = connect(
  state =>
    ({
      almonds: state.almondsList.almonds,
      squares: state.squaresList.squares,
    }),
  dispatch =>
    ({
      onFetchInvitedAlmonds(gameId, userId) {
        dispatch(fetchInvitedAlmonds(gameId, userId));
      },
      onFetchInvitedSquares(gameId, userId) {
        dispatch(fetchInvitedSquares(gameId, userId));
      },
      onDeletePick(gameId, userId, almondId) {
        dispatch(deletePick(gameId, userId, almondId));
      },
      onDragAlmond(almondId) {
        dispatch(canDragAlmond(almondId));
      },
    }),
)(AlmondList);

