export const Tournaments = connect(
  state =>
    ({
      tournaments: state.tournamentsList.tournaments,
    }),
  dispatch =>
    ({
      onFetch(tournamentId) {
        dispatch(fetchTournaments());
        dispatch(fetchGames(tournamentId));
        dispatch(fetchPlayerName());
        dispatch(fetchTime());
      },
      onRemove(id) {
        dispatch(removeTournament(id));
      },
    }),
)(TournamentList);

