import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Container from './Container.jsx';
import Whoops404 from './Whoops404';
import '../../../stylesheets/Play.scss';
import { testState } from '../../../routes/route_callbacks';
// https://stackoverflow.com/questions/40352310/how-do-you-mix-componentdidmount-with-react-redux-connect

class Play extends Component {
  componentWillMount() {
    // alert('*~*~*~*~*~*~*~*~*~*~*~*~componentDidMount for Play *~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~');
    // this.props.onFetch();
    // testState();
  }

  getGameId(game_id, user_id, game_name, location) {
    // use the soccer tourney values here?
    // goal is to not render until the data is loaded

    console.log('*~*~*~*~*~*~*~*~[ServerSide] getGameId for Play *~*~*~*~*~*~*~*~*~*~*~*~*~*~*~*~');
    return (
      ((!game_id) ?
        <Whoops404 location={location} /> :
        <section className="game-details" style={this.style}>
          <h1>{game_name}</h1>
          <Container game_id={game_id} user_id={user_id} />
        </section>)
    );
  }


  render() {
    const {
      game_id, user_id, game_name, location,
    } = this.props;
    return (
      this.getGameId(game_id, user_id, game_name, location)
    );
  }
}

Play.propTypes = {
  user_id: PropTypes.number.isRequired,
  game_id: PropTypes.string.isRequired,
  game_name: PropTypes.string,
  onFetch: PropTypes.func,
};

Play.defaultProps = {
  onFetch: f => f,
};

export default Play;
