
import PropTypes from 'prop-types';
import OwnerGame from './OwnerGame.jsx';
import '../../../stylesheets/OwnerGameList.scss';

const OwnerGameList = ({ gameNames = [] }) =>
  (<div className="game-list">
    {(gameNames.length === 0) ?
      <p>Click Tournament above to show games</p> :
      gameNames.map(game =>
        (<OwnerGame
          key={game.id}
          {...game}
        />))
    }
  </div>
  );

OwnerGameList.propTypes = {
  gameNames: PropTypes.array,
};

export default OwnerGameList;
