import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import FaColumns from 'react-icons/lib/fa/columns';
import '../../../stylesheets/OwnerGame.scss';

class OwnerGame extends Component {
  componentDidMount() {
  }

  componentWillUnmount() {
    // if (this._asyncRequest) {
      // this._asyncRequest.cancel();
    // }
  }

  render() {
    const {
      id, name, available, history,
    } = this.props;
    return (
      <section
        className="ownergame"
        style={this.style}
      >
        <button onClick={() => {
          history.push(`/invite/${id}`);
        }}>
          <FaColumns />
        </button>
        <br />
        <ul>
          <li>
            <h4>{name}</h4>
          </li>
          <li>
            Available Invites: {available}
          </li>
        </ul>
      </section>
    );
  }
}

OwnerGame.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  available: PropTypes.string,
};

OwnerGame.defaultProps = {
  available: 'All sent, order needed',
};

export default withRouter(OwnerGame);
