import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { DragSource, useDrag } from 'react-dnd';
import { compose } from 'redux';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { v4 } from 'uuid';
import { ItemTypes } from './Constants';
import storeFactory from '../../store/index';
import { setPick } from '../../actions';

// Connect the Almond to a color
// https://github.com/LouisBrunner/dnd-multi-backend/blob/master/packages/react-dnd-multi-backend/examples/Card.js


// if the route path contains invitedgame then we can prove that this is an invited user
// invited = true
// path to getAlmonds and getSquares should contain the userId

const clientStore = storeFactory();

function canMoveAlmond(isDropped, isExpired, locked) {
  if (locked !== null) {
    confirmAlert({
      title: `This game has been locked => ${locked}`,
      message: '',
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
  }
  if (isDropped) {
    confirmAlert({
      title: `Almond is already dropped => ${isDropped}`,
      message: '',
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
  }
  if (isExpired === 'expired') {
    confirmAlert({
      title: `This tournament is no longer allowing changes as of ${isExpired}`,
      message: '',
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
  }
  return true;
}

function canDeletePick(
  game_id,
  userId,
  isExpired,
  almond,
  locked,
  onDeletePick,
  onFetchInvitedAlmonds,
  onFetchInvitedSquares,
) {
  console.log(`\n\n\n\n Locked!  ${locked} \n\n\n\n`);
  if ((isExpired !== 'expired') && (locked === null)) {
    onDeletePick(game_id, userId, almond);
    onFetchInvitedAlmonds(game_id, userId);
    onFetchInvitedSquares(game_id, userId);
  }

  return true;
}

/*
          almond_id,
          game_id,
          userId,
          isDropped,
          isExpired,
          locked,
          onDeletePick,
          onFetchInvitedAlmonds,
          onFetchInvitedSquares,
 */

function handleBoxClick(
  almond_id,
  game_id,
  userId,
  isDropped,
  isExpired,
  locked,
  onDeletePick,
  onFetchInvitedAlmonds,
  onFetchInvitedSquares,
) {
  const almond = almond_id;
  const lockCheck = locked;
  // const gameFetch = (game_id, userId, almond);

  console.log(`\n\n\n Lock check${lockCheck} \n\n\n`);
  console.log(typeof lockCheck);

  if (lockCheck) {
    confirmAlert({
      title: 'Locked',
      message: `This game has been locked => ${lockCheck}`,
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
    return (lockCheck);
  }

  if (isExpired === 'expired') {
    confirmAlert({
      title: 'Expired',
      message: `This tournament is no longer allowing changes as of ${isExpired}`,
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
    return (isExpired);
  }

  if (isDropped === null) {
    confirmAlert({
      title: 'Not dropped',
      message: `Drag and drop to a square below. ${userId}`,
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
    return (isDropped);
  }

  confirmAlert({
    title: `Reset almond #${almond}?`,
    message: 'Remove this pick?',
    buttons: [
      {
        label: 'Yes',
        onClick: () => {
          canDeletePick(
            game_id,
            userId,
            isExpired,
            almond,
            locked,
            onDeletePick,
            onFetchInvitedAlmonds,
            onFetchInvitedSquares,
          );
        },
      },
      {
        label: 'No',
        onClick: () => {},
      },
    ],
  });
}

function updateDroppedAlmond(almond_id, game_id, userId, onFetchInvitedAlmonds) {
  const almond = almond_id;
  // const userId = this.state.userId;

  const gameFetch = (game_id, userId);
  onFetchInvitedAlmonds(game_id, userId);
}

function updateDroppedSquare(almond_id, game_id, userId, onFetch) {
  const almond = almond_id;
  const gameFetch = game_id;
  onFetch(gameFetch);
}

const almondSource = {
  canDrag(props) {
    const dragResult = canMoveAlmond(props.isDropped, props.isExpired, props.locked);
    return {
      dragResult,
    };
  },
  beginDrag(props) {
    return {
      almond_id: props.almond_id,
    };
  },
  endDrag(props, monitor) {
    const dropResult = monitor.getDropResult();

    if (dropResult) {
      const pickId = v4();
      const droppedSquare = dropResult.name;

      const userPick = {
        id: pickId,
        userId: props.userId,
        gameId: props.game_id,
        squareId: droppedSquare,
        almondId: props.almond_id,
      };

      clientStore.dispatch(setPick(
        userPick.id,
        userPick.gameId,
        userPick.userId,
        userPick.almondId,
        userPick.squareId,
      ));
      updateDroppedAlmond(
        props.almond_id,
        props.game_id,
        props.userId,
        props.onFetchInvitedAlmonds,
      );
      updateDroppedSquare(
        props.almond_id,
        props.game_id,
        props.userId,
        props.onFetchInvitedSquares,
      );
    }
  },
};

const enhance = compose(DragSource(ItemTypes.ALMOND, almondSource, (connect, monitor) => ({
  connectDragSource: connect.dragSource(),
  isDragging: monitor.isDragging(),
  dropResult: monitor.getDropResult(),
})));


class Almond extends PureComponent {
  render() {
    const {
      almond_id,
      game_id,
      userId,
      isDragging,
      isDropped,
      isExpired,
      locked,
      connectDragSource,
      onDeletePick,
      onFetchInvitedAlmonds,
      onFetchInvitedSquares,
    } = this.props;

    return connectDragSource(<div id="eachalmond"
      style={{
        opacity: isDragging ? 0.5 : 1,
        border: '.75px dashed gray',
        backgroundColor: 'white',
        padding: '0.5rem 1rem',
        marginRight: '1.5rem',
        marginBottom: '1.5rem',
        cursor: 'pointer',
        float: 'left',
      }}
      onClick={handleBoxClick.bind(
 this,
          almond_id,
          game_id,
          userId,
          isDropped,
          isExpired,
          locked,
          onDeletePick,
          onFetchInvitedAlmonds,
          onFetchInvitedSquares,
        )}
    >
      {
        isDropped ?
          <i><s>{almond_id}</s></i> :
          almond_id
      }
                             </div>);
  }
}

Almond.propTypes = {
  connectDragSource: PropTypes.func.isRequired,
  isDragging: PropTypes.bool.isRequired,
  almond_id: PropTypes.number.isRequired,
  type: PropTypes.string.isRequired,
  isDropped: PropTypes.number,
  isExpired: PropTypes.string.isRequired,
  locked: PropTypes.string.isRequired,
  game_id: PropTypes.string.isRequired,
  userId: PropTypes.string.isRequired,
  onFetchInvitedAlmonds: PropTypes.func.isRequired,
  onFetchInvitedSquares: PropTypes.func.isRequired,
  onDeletePick: PropTypes.func.isRequired,
};

export default enhance(Almond);
