import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../../../stylesheets/ThankYouPage.scss';

function signMeUp() {
  location.replace("https://wildalmonds.com/signup")
}

class ThankYouPage extends Component {
  state = {
    gameId: this.props.match.params.gameId,
    userId: this.props.match.params.userId,
    gameName: this.props.gameName,
    onLock: this.props.onLock,
  };

  componentDidMount() {
    this._asyncRequest = this.props.onLock(this.state.gameId, this.state.userId);
    setTimeout(() => { // Brute force fix if user hits refresh
      this.setState({
        gameName: this.state.gameName,
      });
    }, 250);
  }

  componentWillUnmount() {
    if (this._asyncRequest) {
      this._asyncRequest.cancel();
      this.setState({
        gameName: null,
      });
    }
  }

  render() {
    const {
      gameName,
    } = this.props;
    return (
      (
        <div id="thankyou" style={this.style}>
          <div className="wrapper">
            <h1>{gameName}</h1>
            <ul>
              <li>Wow!  You so totally rocked that!</li>
              <li>Your picks are locked and safe. In the vault.</li>
              <li>Please consider WildAlmonds for your survey needs.</li>
              <li>We can customize to meet your particular business opportunity.</li>
              <br />
              <br />
              <button id="create"
                      onClick={() => {
                        signMeUp();
                      }}
              >Signup
              </button>
              <br />
              <li>We'd love your feedback too!</li>
              <li><a href="mailto:wildalmondz@gmail.com">wildalmonds</a></li>
            </ul>
          </div>
        </div>)
    );
  }
}

ThankYouPage.propTypes = {
  gameName: PropTypes.string.isRequired,
  onLock: PropTypes.func,
};

ThankYouPage.defaultProps = {
  gameName: "Thank You!!",
  onLock: f => f,
};

export default ThankYouPage;
