import React from 'react';
import PropTypes from 'prop-types';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { DropTarget } from 'react-dnd';
import { ItemTypes } from './Constants';

const createReactClass = require('create-react-class');

const squareTarget = {
  canDrop(props) {
    if (props.lastDroppedItem) {
      return false;
    }
    return true;
  },
  drop(props, monitor) {
    // 2/21/19 Line below needed to print Almond name on Square
    props.onDrop(monitor.getItem());
    // needed to return the square_id back to Almond endDrag
    return { name: props.square_id };
  },
};

function handleSquareClick(square_name, lastDroppedItem) {
  let lastItem = 'none. This square is available.';

  if (lastDroppedItem !== null) {
    lastItem = lastDroppedItem;
  }

  confirmAlert({
    title: `${square_name}`,
    message: `The bigger the number, the higher your preference. Selection is ${lastItem}`,
    buttons: [
      {
        label: 'Ok',
        onClick: () => {},
      },
    ],
  });
}

function collect(connect, monitor) {
  return {
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    canDrop: monitor.canDrop(),
    getDropResult: monitor.getDropResult(),
    itemType: monitor.getItem(),
  };
}

const BoardSquare = createReactClass({
  propTypes: {
    square_name: PropTypes.string.isRequired,
    isOver: PropTypes.bool.isRequired,
    canDrop: PropTypes.bool.isRequired,
    onDrop: PropTypes.func.isRequired,
    getDropResult: PropTypes.object,
    lastDroppedItem: PropTypes.number,
    connectDropTarget: PropTypes.func.isRequired,
    children: PropTypes.node,
  },

  render() {
    const {
      square_name, children, square_rank, isOver, canDrop, connectDropTarget, lastDroppedItem,
    } = this.props;

    const isActive = isOver && canDrop;

    let backgroundColor = '#222';
    if (isActive) {
      backgroundColor = 'darkgreen';
    } else if (canDrop !== false) {
      backgroundColor = 'darkkhaki';
    }
    if (lastDroppedItem) {
      backgroundColor = 'darkred';
    }

    return connectDropTarget(<div
      style={{
                height: '5rem',
                width: '5rem',
                marginRight: '.5rem',
                marginBottom: '.5rem',
                color: 'white',
                padding: '1rem',
                textAlign: 'center',
                fontSize: '1rem',
                lineHeight: '.8rem',
                float: 'left',
                backgroundColor,
            }}
      onClick={handleSquareClick.bind(this, square_name, lastDroppedItem)}
    >
      {children}
      <p>{square_rank !== 0 ? square_rank : ''}</p>
      <p>{square_name}</p>
      {isActive ?
         'Release' :
         ''
      }
      {lastDroppedItem &&
      <p> {JSON.stringify(lastDroppedItem)} </p>
                }
    </div>);
  },
});

module.exports = DropTarget(ItemTypes.ALMOND, squareTarget, collect)(BoardSquare);
