// https://github.com/rajaraodv/react-redux-blog
// https://medium.com/@rajaraodv/a-guide-for-building-a-react-redux-crud-app-7fe0b8943d0f

export const pick = (state = {}, action={ type: null }) => {
    switch (action.type) {
        case C.ADD_PICK_WRKS:
            return {
                almondId: action.almondId,
                squareId: action.squareId,
            }
        case C.RATE_PICK:
            return (state.id !== action.id) ? state :
            {
                ...state,
                rating: action.rating
            }
        default :
            return state
    }
}

export const almond = (state = {}, action={ type: null }) => {
    switch (action.type) {
        case C.CLIENT_PICK:
            return (state.id !== action.id) ? state :
            {
                ...state,
                rating: action.rating
            }
        default :
            return state
    }
}

