export const almondsList = (state = {}, action={ type: null
 }) => {
           switch (action.type) {
               case C.FETCH_ALMONDS:
                   return {
                         almonds:[], error: null, loading:true
                   }
               case C.FETCH_ALMONDS_SUCCESS: // return list of almonds and make loading = false
                   return {
                        ...state,
                        almonds: action.payload, error:null, loading: false
                   }
                case C.BUG_CLIENT_PICKS :
                    return state.map(
                        c => almond(c, action)
                )
               default :
                   return state
           }
}

