import { createStore, combineReducers, applyMiddleware } from 'redux';
import {
  addTournament,
  almondsList,
  gamesList,
  invitesList,
  ordersList,
  ownerList,
  ownerReportsList,
  scoresList,
  squaresList,
  tournamentsList,
  servertime,
  expiretime,
  player,
  gameplayers,
  picksremain,
  lockedstatus,
  invitedUser,
  invitedGame,
  invitedFrom,
  message,
} from './reducers.js';
import thunk from 'redux-thunk';

const clientLogger = store => next => (action) => {
  if (action.type) {
    let result;
    console.groupCollapsed('client side dispatching', action.type);
    console.log('prev state', store.getState().gamesList.games);
    console.log('action', action);
    result = next(action);
    console.log('next state', store.getState().gamesList.games);
    console.groupEnd();
    return result;
  }
  return next(action);
};

const serverLogger = store => next => (action) => {
  console.log('current state', store.getState());
  console.log('\n  dispatching server action\n');
  console.log(action);
  console.log('\n');
  console.log('next state', store.getState());
  return next(action);
};

const middleware = server => [
  (server) ? serverLogger : clientLogger,
  thunk,
];

const storeFactory = (server = true, initialState) =>
  applyMiddleware(...middleware(server))(createStore)(
    combineReducers({
      addTournament,
      almondsList,
      gamesList,
      invitesList,
      ordersList,
      ownerList,
      ownerReportsList,
      scoresList,
      squaresList,
      tournamentsList,
      servertime,
      expiretime,
      player,
      gameplayers,
      picksremain,
      lockedstatus,
      invitedUser,
      invitedGame,
      invitedFrom,
      message,
    }),

    initialState,
  );

export default storeFactory;
