import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { confirmAlert } from 'react-confirm-alert';
import '../../../stylesheets/AddUserForm.scss';

import { MessageBoardId } from '../containers';


let invitesChk;

function reloadMe() {
  location.replace('https://wildalmonds.com/owner/dashboard');
}

class AddInvitesForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      invites: '',
      orders: this.props.orders,
      gameId: this.props.game_id,
      onCheckOrder: this.props.onCheckOrder,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    // this._asyncRequest = findExistingOrder(this.state.onCheckOrder, this.props.game_id);
  }

  componentWillUnmount() {
    // alert(this.state.orders);
    // if (this._asyncRequest) {
    //  this._asyncRequest.cancel();
    // }
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    this.props.onSendOrder(
      this.state.invites,
      this.state.gameId,
    );

    confirmAlert({
      title: 'Send Invite',
      message: 'Thank you for your request. We will be contacting you via email soon',
      buttons: [
        {
          label: 'Ok',
          onClick: () => { reloadMe(); },
        },
      ],
    });
  }

  render() {
    const {
      orders,
    } = this.props;

    if (orders === undefined) {
      invitesChk =
        (
          <section>
            <div id="add-user" style={this.style}>
              <h3>[ Order Invites]</h3>
              <div id="messageboard">
                <MessageBoardId />
              </div>
              <div id="invites">
                <div>
                  <label htmlFor="order-invites">Number of Invites (10-5000)</label>
                  <form onSubmit={this.handleSubmit} method="post">
                    <input
                      id="order-invites"
                      label="Number of invites"
                      name="invites"
                      type="number"
                      min="10"
                      max="5000"
                      className="form-control"
                      value={this.state.invites}
                      onChange={this.handleInputChange}
                      required
                    />
                    <p>
                      We will contact you regarding pricing for your request as soon as possible.
                      <br />
                      Thank you for your support!
                      <br />
                    </p>
                    <input name="addit" type="submit" defaultValue="Sign up" className="btn btn-primary btn-block" />
                  </form>
                </div>
              </div>
            </div>
          </section>
        );
    } else {
      invitesChk = <div><strong>Order for invites in the queue</strong></div>;
    }

    return (
      <div className="email-details">{invitesChk}</div>
    );
  }
}

AddInvitesForm.propTypes = {
  game_id: PropTypes.string.isRequired,
  orders: PropTypes.array,
  onMessage: PropTypes.func,
  onCheckOrder: PropTypes.func,
  onFetchAvailableInvites: PropTypes.func,
  onSendOrder: PropTypes.func,
};

AddInvitesForm.defaultProps = {
  onMessage: f => f,
  onCheckOrder: f => f,
  onSendOrder: f => f,
};

export default withRouter(AddInvitesForm);
