import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { ContainerId } from '../containers';
import '../../../stylesheets/GameDetails.scss';

class InvitedGameDetails extends Component {
  state = {
    gameId: this.props.match.params.gameId,
    userId: this.props.match.params.userId,
    gameName: null,
    gameDescription: null,
    onFetchInvitedGame: this.props.onFetchInvitedGame,
  };

  componentDidMount() {
    this._asyncRequest = this.props.onFetchInvitedGame(this.state.gameId, this.state.userId);
    setTimeout(() => { // Brute force fix if user hits refresh
      this.setState({
        gameName: this.props.games[0].game_name,
        gameDescription: this.props.games[0].game_description,
      });
    }, 250);
  }

  componentWillUnmount() {
    if (this._asyncRequest) {
      this._asyncRequest.cancel();
      this.setState({
        gameName: null,
        gameDescription: null,
      });
    }
  }

  render() {
    return (
      (
          <div id="game-details">
            <br />
            <br />
            <h1>Event Name: {this.state.gameName}</h1>
            <h3>Description: {this.state.gameDescription}</h3>
            <ul>
              <li>Click or touch and hold a number (a.k.a Almond) below and drag to your selection</li>
              <li>The bigger the number, the higher your preference</li>
              <li>When Picks Remain below is 0, click the Lock picks button to complete selection</li>
              <li>Single Click or tap an Almond or Square for more details about how to play</li>
            </ul>
            <ContainerId game_id={this.props.match.params.gameId} userId={this.props.match.params.userId} />
          </div>)
    );
  }
}

InvitedGameDetails.propTypes = {
  game_id: PropTypes.string,
  game_name: PropTypes.string,
  game_description: PropTypes.string,
  onFetchInvitedGame: PropTypes.func,
};

InvitedGameDetails.defaultProps = {
  game_name: "Reload",
  game_description: "Reload",
  onFetchInvitedGame: f => f,
};

export default InvitedGameDetails;
