import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { confirmAlert } from 'react-confirm-alert';
import FaQuestion from 'react-icons/lib/fa/question-circle-o';
import { v4 } from 'uuid';
import '../../../stylesheets/InvitePendingForm.scss';

class InvitePending extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      gameId: this.props.game_id,
      gamename: this.props.gamename,
      inviteId: this.props.invite_id,
      marker: this.props.marker,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  helpMessage = () => {
    // alert(`Hello! ${history}`);
    confirmAlert({
      title: 'Send Invite',
      message: 'Send your next available invite. ' +
      'As you send them, the next queued invite ' +
      'will move up and become available',
      buttons: [
        {
          label: 'Ok',
          onClick: () => {},
        },
      ],
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    const userId = v4();
    const emailId = v4();

    this.props.onSendInvite(
      userId,
      this.state.gameId,
      this.state.gamename,
      this.state.value,
      emailId,
      this.state.inviteId,
    );
    setTimeout(() => { // Brute force fix for Dashboard load
      this.props.onFetchAvailableInvites(this.state.gameId);
      this.props.onFetchOwnerReport(this.state.gameId);
    }, 1200);
  }

  render() {
    const {
    } = this.props;

    let emailOk;
    if ((this.state.marker === 1) && (this.props.expired !== 'expired')) {
      emailOk =
      (
        <form onSubmit={this.handleSubmit}>
          <label><strong>Email:</strong></label>
          <input type="email" value={this.state.value} onChange={this.handleChange} required />
          <input name="addit" type="submit" defaultValue="Send" className="btn btn-primary btn-block" />
        </form>
      );
    } else {
      emailOk = <div><strong>Invite in queue #{this.state.marker}</strong></div>;
    }

    return (
      <section className="invitePending" style={this.style}>
        <div className="name-details">
          <form onSubmit={this.handleSubmit}>
            <ul>
              <li>
                <label>
                  <strong>Game Name:</strong> {this.props.name}
                </label>
              </li>
              <li>
                <label>
                  <strong>Expired:</strong> [ {this.props.expired} ]
                </label>
              </li>
            </ul>
          </form>
          <div className="email-details">{emailOk}</div>
        </div>
      </section>
    );
  }
}

InvitePending.propTypes = {
  marker: PropTypes.string.isRequired,
  expired: PropTypes.string.isRequired,
  gamename: PropTypes.string.isRequired,
  status: PropTypes.string.isRequired,
  invite_id: PropTypes.string.isRequired,
  game_id: PropTypes.string.isRequired,
  onSendInvite: PropTypes.func,
  onFetchAvailableInvites: PropTypes.func,
  onFetchOwnerReport: PropTypes.func,
};

InvitePending.defaultProps = {
  onSendInvite: f => f,
  onFetchAvailableInvites: f => f,
  onFetchOwnerReport: f => f,
};


export default withRouter(InvitePending);
