import { Component } from 'react';
import PropTypes from 'prop-types';

import FaTrash from 'react-icons/lib/fa/trash-o';
import '../../../stylesheets/Tournament.scss';
import { CountdownId, Games } from '../containers';

class Tournament extends Component {
  componentWillMount() {

  }
  render() {
    const {
      tournament_id, tournament_name, expires, onFetch,
    } = this.props;
    return (
      <section className="tournament" style={this.style}>
        <h1
          ref="tournament_name"
          onClick={onFetch}
        >{tournament_name}
        </h1>
        <CountdownId expires={expires} />
        <Games tournament_id={tournament_id} />
        <button onClick={onFetch}>
          <FaTrash />
        </button>
        <div
          className="tournament"
          onClick={onFetch}
        />
      </section>
    );
  }
}

Tournament.propTypes = {
  tournament_id: PropTypes.string.isRequired,
  tournament_name: PropTypes.string.isRequired,
  expires: PropTypes.string,
  onFetch: PropTypes.func,
  onRemove: PropTypes.func,
};

Tournament.defaultProps = {
  onFetch: f => f,
  onRemove: f => f,
};

// export default withRouter(Tournament);
export default (Tournament);

