import PropTypes from 'prop-types';
import Game from './Game.jsx';
import '../../../stylesheets/GameList.scss';

const GameList = ({ games = [], expires, onFetch = f => f, onRemove = f => f }) =>
  (<div className="game-list">
    {(games.length === 0) ?
      <p>Click Tournament above to show games</p> :
      games.map(game =>
        (<Game
          key={game.game_id}
          expires={expires}
          {...game}
          onRemove={() => onRemove(game.game_id)}
          onFetch={() => onFetch(game.game_id)}
        />))

    }
  </div>
  );

GameList.propTypes = {
  games: PropTypes.array,
  expires: PropTypes.string,
  onFetch: PropTypes.func,
  onRemove: PropTypes.func,
};

export default GameList;
