import React from 'react';
import PropTypes from 'prop-types';
import OrderPending from './OrderPending.jsx';
import { AddInvitesFormId } from '../containers';
import '../../../stylesheets/TournamentList.scss';

const OrderPendingList = ({
  orders = [], game_id,
}) =>
  (<div className="tournament-list">
    {(orders.length === 0) ?
      <AddInvitesFormId game_id={game_id} /> :
            orders.map(order =>
                (<OrderPending
                  key={order.order_id}
                  invites={order.invites}
                  requestMade={order.requested}
                />))
        }
  </div>);

OrderPendingList.propTypes = {
  game_id: PropTypes.string,
  orders: PropTypes.array,
};

export default OrderPendingList;
