import React from 'react';
import PropTypes from 'prop-types';
import '../../../stylesheets/SendInvitePage.scss';
import GameReportDetails from './GameReportDetails.jsx';

const OwnerReportsList = ({
  reports = [],
}) =>
  (<div>
    {(reports.length === 0) ?
      <p>...checking for current game</p> :
    reports.map(report =>
    (<GameReportDetails
      {...report}
      key={report.id}
      gameName={report.gameName}
      description={report.description}
      expires={report.expires}
    />))
  }</div>);

OwnerReportsList.propTypes = {
  reports: PropTypes.array,
  scores: PropTypes.array,
};

OwnerReportsList.defaultProps = {
};

export default OwnerReportsList;
