import React from 'react';
import PropTypes from 'prop-types';
import OwnerReport from './OwnerReport.jsx';
import '../../../stylesheets/OwnerReportsList.scss';

const OwnerReportsList = ({
  reports = [],
}) =>
  (<div id="reports-list">
    {
(reports.length === 0) ?
  <p>...checking for available reports</p> :
reports.map(report =>
  (<OwnerReport
    {...report}
    key={report.id}
    id={report.id}
    expires={report.expires}
    scores={report.scores}
  />))
}
  </div>);

OwnerReportsList.propTypes = {
  reports: PropTypes.array,
  scores: PropTypes.array,
};

OwnerReportsList.defaultProps = {
};

export default OwnerReportsList;
